"use client";

import { useState } from "react";

export default function PostOpportunity() {
  const [type, setType] = useState("");
  const [showOther, setShowOther] = useState(false);
  const [pdfFile, setPdfFile] = useState<File | null>(null);

  // Form fields
  const [form, setForm] = useState({
    title: "",
    description: "",
    fieldOfStudy: "",
    duration: "",
    prerequisites: "",
    type: "",
    otherType: "",
    institute: "",
    professorName: "",
  });

  const handleChange = (e: any) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleTypeChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const value = e.target.value;
    setType(value);
    setShowOther(value === "Other");
    setForm({ ...form, type: value });
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (!pdfFile) {
      alert("Please upload the PDF file");
      return;
    }

    try {
      // Build JSON expected by backend
      const opportunityData = {
        title: form.title,
        description: form.description,
        fieldOfStudy: form.fieldOfStudy,
        type: form.type === "Other" ? form.otherType : form.type,
        duration: form.duration,
        prerequisites: form.prerequisites,
        professor: {
          id: 4, // HARD CODED for now (change later after login)
        },
      };

      // Convert JSON to string
      const formData = new FormData();
      formData.append("data", JSON.stringify(opportunityData));
      formData.append("file", pdfFile);

      const res = await fetch(
        "http://localhost:8082/api/professor/opportunities/create",
        {
          method: "POST",
          body: formData,
        }
      );

      if (res.ok) {
        alert("üéâ Opportunity posted successfully!");
        e.currentTarget.reset();
        setType("");
        setShowOther(false);
        setPdfFile(null);
      } else {
        const error = await res.text();
        alert("‚ùå Failed: " + error);
      }
    } catch (error) {
      console.error("Upload Error:", error);
      alert("‚ùå Something went wrong");
    }
  };

  return (
    <div className="bg-gray-50 min-h-screen flex flex-col">
      {/* Header */}
      <header className="bg-gradient-to-r from-indigo-600 to-blue-600 text-white text-center py-4 shadow-md">
        <h1 className="text-2xl font-bold">ProfConnect - Post Opportunity</h1>
      </header>

      {/* Form */}
      <main className="flex-grow flex items-center justify-center p-6">
        <form
          onSubmit={handleSubmit}
          className="bg-white shadow-lg rounded-2xl p-8 w-full max-w-lg space-y-5"
        >
          {/* Title */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">
              Opportunity Title
            </label>
            <input
              name="title"
              type="text"
              placeholder="Enter title"
              required
              onChange={handleChange}
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
            />
          </div>

          {/* Description */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">
              Description
            </label>
            <input
              name="description"
              type="text"
              placeholder="Short description"
              required
              onChange={handleChange}
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
            />
          </div>

          {/* Field of Study */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">
              Field of Study
            </label>
            <input
              name="fieldOfStudy"
              type="text"
              placeholder="e.g. AI/ML"
              required
              onChange={handleChange}
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
            />
          </div>

          {/* Opportunity Type */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">
              Type of Opportunity
            </label>
            <select
              value={type}
              onChange={handleTypeChange}
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
            >
              <option value="">Select</option>
              <option value="RA">Research Assistantship (RA)</option>
              <option value="TA">Teaching Assistantship (TA)</option>
              <option value="Internship">Internship</option>
              <option value="Other">Other</option>
            </select>
          </div>

          {/* Other Type */}
          {showOther && (
            <div>
              <label className="block font-medium mb-1 text-gray-700">
                Please Specify
              </label>
              <input
                name="otherType"
                type="text"
                placeholder="Enter type"
                onChange={handleChange}
                className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
              />
            </div>
          )}

          {/* Duration */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">
              Duration
            </label>
            <input
              name="duration"
              type="text"
              placeholder="e.g. 3 months"
              required
              onChange={handleChange}
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
            />
          </div>

          {/* Pre-requisites */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">
              Pre-requisites
            </label>
            <textarea
              name="prerequisites"
              placeholder="List required skills/courses"
              rows={3}
              required
              onChange={handleChange}
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
            ></textarea>
          </div>

          {/* File Upload */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">
              Upload Detailed PDF
            </label>
            <input
              type="file"
              accept="application/pdf"
              onChange={(e) => setPdfFile(e.target.files?.[0] || null)}
              className="w-full border rounded-lg px-3 py-2 bg-gray-50"
            />
          </div>

          {/* Submit */}
          <div className="text-center">
            <button
              type="submit"
              className="bg-indigo-600 text-white px-6 py-2 rounded-lg hover:bg-indigo-700 transition"
            >
              Post Opportunity
            </button>
          </div>
        </form>
      </main>
    </div>
  );
}
